/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * *
 * Display
 */
 .calc-display {
   font-size: 1.1rem;

   padding: 28px 0 15px;
}

.calc-display > input {
   text-align: right;

   display: block;
   width: 100%;

   background: hsl(0, 0%, 100%);
   border: none;

   overflow: hidden;
}

.calc-display > input:first-child {
   font-size: 3.0em;
   line-height: 1.2;

   padding: 0 18px;

   color: hsl(0, 0%, 21%);

   overflow: hidden;
}

.calc-display > input:last-child {
   font-size: 1.2em;
   line-height: 1.7;

   padding: 0 20px;

   color: hsl(0, 0%, 72%);

   overflow: hidden;
}

/* * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * * *
 * Control
 */
.calc-control {
   font-size: 1rem;
}

.calc-control::after {
   content: '';
   display: block;
   clear: both;
}

.calc-control > input {
   font-size: 0.96em;

   display: block;
   width: 75px;
   height: 69px;

   float: left;

   color: hsl(0, 0%, 21%);
   background: hsl(0, 0%, 100%);
   border: none;
   border-right: 1px solid hsl(0, 0%, 93%);
   border-bottom: 1px solid hsl(0, 0%, 93%);
   outline: none;

   cursor: pointer;
}

.calc-control > input::-moz-focus-inner {
   border: 0;
}

.calc-control > input[value='0'],
.calc-control > input[value='='] {
   width: 150px;
}

.calc-control > input[value='AC'],
.calc-control > input[value='CE'],
.calc-control > input[value='='] {
   border-top: 1px solid hsl(0, 0%, 93%);
}

.calc-control > input[value='='],
.calc-control > input[value='รท'],
.calc-control > input[value='x'],
.calc-control > input[value='-'],
.calc-control > input[value='+'] {
   border-right: none;
}

.calc-control > input[value='0'],
.calc-control > input[value='.'],
.calc-control > input[value='รท'] {
   border-bottom: none;
}

.calc-control > input.down {
   background: hsl(0, 0%, 93%);
}


